import Cookies from 'js-cookie'
import axios from 'axios'
export default {
  name: 'missionDetailDetail',
  data () {
    return {
      fileList: [],
      gainID: '',
      level: parseInt(Cookies.get('level')),
      imageUrl: '',
      gain: {
        MissionID: parseInt(sessionStorage.getItem('missionID')),
        Name: '',
        Remark: ''
      },
      addGain: false,
      mission: [],
      gainList: []
    }
  },
  methods: {
    uploadSuccess () {
      alert('上传成功！')
      this.getGainList()
    },
    uploadError () {
      alert('上传失败！请稍后再试或联系助理')
    },
    goGain (item) {
      sessionStorage.setItem('gainName', item.name)
      sessionStorage.setItem('gainID', item.id)
      this.$router.push('/gain')
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 / 1024 < 3

      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 3MB!')
      }
      return isLt2M
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    handleAddGain () {
      this.gain.Name = ''
      this.gain.Remark = ''
      this.addGain = true
    },
    submitGain () {
      this.$refs.upload.submit()
      this.addGain = false
      axios.get(this.url + '/gain/mission/' + sessionStorage.getItem('missionID'))
        .then(this.getGainListSucc)
    },
    getGainList () {
      axios.get(this.url + '/gain/mission/' + sessionStorage.getItem('missionID'))
        .then(this.getGainListSucc)
    },
    getGainListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.gainList = res
      }
    },
    getMission () {
      axios.get(this.url + '/mission/id/' + sessionStorage.getItem('missionID'))
        .then(this.getMissionSucc)
    },
    getMissionSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.mission = res
      }
    }
  },
  mounted () {
    this.getMission()
    this.getGainList()
  }
}



